.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 15px;
  position: fixed;
  z-index: 10;

  h1 {
    font-size: 1.5rem;
    color: rgb(255, 0, 0);
    cursor: pointer;
  }

  svg {
    width: 1.5rem;
    color: #fff;
  }
}

.background_scrolled {
  background-color: #000;
  transition: 0.5s ease-in;
}
