.container {
  padding: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 25px;
  color: #fff;

  b {
    display: block;
  }
}

.text {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.line {
  height: 2px;
  background-color: #828282;
}

.video {
  iframe {
    border: none;
    box-shadow: 2px 2px 1px rgb(0, 0, 0);
  }
}

.imageVideo {
  width: 100%;
  height: 400px;
  background-position: center;
  background-size: cover;
}

.no_trailer {
  width: 100%;
  height: 400px;
  background-color: #000;
  display: flex;
  justify-content: center;
  align-items: center;
}
