.container {
  display: flex;
  flex-direction: column;
  gap: 10px;
  color: #fff;
  margin: 20px 0 40px 0;

  h2 {
    font-size: 1.3rem;
  }
}

.cover_list_movie {
  padding: 0 10px;
}

.list_movie {
  padding: 10px 0;
  display: flex;
  gap: 5px;
  overflow-x: auto;

  &::-webkit-scrollbar {
    height: 5px;
  }

  &::-webkit-scrollbar-track {
    background: #182125;
  }

  &::-webkit-scrollbar-thumb {
    background: #444444;
  }

  &::-webkit-scrollbar-thumb:hover {
    background: #9e9e9e;
  }
}
