.container {
  height: 250px;
  display: flex;
  align-items: flex-end;
  justify-content: center;

  form {
    background-color: #fff;
    width: 40%;
  }
}

.import {
  padding: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  border-bottom: solid 2px #00bbec;

  input {
    border: none;
    background-color: #00bdec13;
    flex-grow: 1;
    height: 1.5rem;
  }

  input:focus {
    outline: solid 1px #00bdec45;
  }

  input:focus::placeholder {
    color: transparent;
  }

  svg {
    height: 1.5rem;
  }
}

.action {
  height: 90px;
  padding: 0 25px;
  display: flex;
  justify-content: flex-end;
  align-items: center;

  button {
    border: none;
    padding: 10px 8px;
  }
}

.reset_btn {
  background-color: inherit;
}

.reset_btn:hover {
  background-color: #cfffa8;
}

.search_btn {
  background-color: #00bbec;
  color: #fff;
}

.search_btn:hover {
  background-color: rgb(12, 143, 208);
}
