.poster_browse {
  width: calc((100% - (5px * (10 - 1))) / 10);
}

.poster_search {
  width: calc((100% - (10px * (9 - 1))) / 9);
}

.poster {
  aspect-ratio: 2/3;
  flex-shrink: 0;
  transition: transform 0.3s;
  z-index: 1;
  background-position: center;
  background-size: cover;
}

.poster:hover,
.backdrop:hover {
  transform: scale(1.1);
  z-index: 2;
}

.backdrop {
  width: 12.5%;
  aspect-ratio: 3/2;
  flex-shrink: 0;
  transition: transform 0.3s;
  z-index: 1;
  background-position: center;
  background-size: cover;
}
