.container {
  height: 400px;
  background-position: 50% 30%;
  background-size: cover;
}

.info_Movie {
  background-color: #00000011;
  width: 25%;
  border-radius: 4px;
  color: #fff;
  margin: 150px 0 15px 15px;
  padding: 5px;

  h2 {
    font-size: 2rem;
    margin-bottom: 20px;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  button {
    background-color: rgba(198, 198, 198, 0.262);
    color: #fff;
    padding: 5px 15px;
    margin-right: 5px;
    border: none;
    border-radius: 2px;
  }

  p {
    font-size: 0.8rem;
    margin-top: 10px;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
